a div {
    display: flex;
    align-items: center;
    gap: 5px;
    color: var(--text-color);
}

.links {
    display: flex;
    flex-wrap: wrap;
    column-gap: 1.5rem;
    font-size: .85rem;
    padding: 0.2rem 0 0.2rem 0;
}

header h1 {
    font-size: 2.5rem;
    line-height: 100%;
}

header h3 {
    font-size: 1.5rem;
}